@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Light.woff2") format("woff2"), url("../fonts/Montserrat-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #de3e3e;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 15px;
  margin: 0 auto;
}

.header {
  position: absolute;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  padding: 40px 0 0;
  max-width: 1200px;
  margin: 0 auto;
}

.header__logo {
  color: #ffffff;
  margin-right: auto;
  order: 0;
}
.header__logo:hover {
  color: #1c3374;
}
.header__logo:focus {
  color: #1c3374;
}
.header__logo:active {
  color: #0266c1;
}
.header__nav {
  order: 1;
}

.header__nav-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 21px;
  max-width: 600px;
}
.header__nav-list a {
  padding: 0 5px;
  color: #ffffff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
}
.header__nav-list a:hover {
  border-bottom: 1px solid #ffffff;
}
.header__nav-list a:not(:disabled):focus {
  outline: 1px solid #ffffff;
}
.header__contact {
  color: #ffffff;
  font-size: 18px;
  line-height: 18px;
  font-weight: 600;
  margin-left: auto;
  order: 2;
}
.header__contact:hover {
  color: #1c3374;
}
.header__contact:focus {
  color: #1c3374;
}
.header__contact:active {
  color: #0266c1;
}
.header__nav-button {
  display: none;
}

@media (max-width: 1199px) {
  .header__container {
    display: flex;
    flex-wrap: wrap;
    padding: 30px 30px 70px 30px;
    max-width: 100%;
    margin: 0;
    width: 100%;
    align-items: center;
  }
  .header__logo {
    color: #ffffff;
    margin: 0 auto;
    order: 1;
  }
  .header__logo svg {
    width: 144px;
    height: 44px;
  }
  .header__nav {
    order: 3;
    width: 100%;
    margin-top: 50px;
  }
  .header__nav-list {
    flex-direction: column;
    gap: 16px;
    max-width: 100%;
  }
  .header__nav-list a {
    position: relative;
    display: flex;
    color: #2d383f;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
    width: fit-content;
    padding: 5px 0;
    z-index: 5;
  }
  .header__nav-list a:hover {
    border-bottom: 1px solid #2d383f;
    padding: 5px 0 4px;
  }
  .header__nav-list a:not(:disabled):focus {
    outline: 1px solid #2d383f;
  }
  .header__contact {
    order: 2;
    margin: 0;
  }
  .header__nav-button {
    position: relative;
    display: flex;
    order: 0;
    border: none;
    background-color: inherit;
    margin-right: auto;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .header__nav-button::before {
    position: absolute;
    content: "";
    background-image: url("../img/svg/burger.svg");
    width: 26px;
    height: 19px;
    background-repeat: no-repeat;
    fill: #ffffff;
    background-position: center;
  }
  .header__nav-button::after {
    position: absolute;
    content: "";
    background-image: url("../img/svg/burger-close.svg");
    width: 26px;
    height: 19px;
    background-repeat: no-repeat;
    background-position: center;
  }
  .header__nav-button span {
    display: none;
  }
  .header__nav-button:hover {
    opacity: 0.8;
  }
  .header__nav-button:focus {
    opacity: 0.6;
  }
  .header__nav-button:active {
    opacity: 0.4;
  }
  .header__nav-button:disabled {
    opacity: 0.2;
    cursor: none;
  }
  .header__nav--nojs + .header__button {
    display: none;
  }
  .header__nav--nojs .header__nav-list {
    display: flex;
  }
  .header__nav--closed .header__nav-list {
    display: none;
  }
  .header__nav--opened .header__nav-list {
    display: flex;
  }
  .header__nav--closed ~ .header__nav-button::after {
    display: none;
  }
  .header__nav--opened ~ .header__nav-button::before {
    display: none;
  }
  .header__nav--opened ~ .header__nav-button::after {
    display: block;
  }
  .header__nav--opened::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    top: 0;
    left: 0;
  }
  .header__nav--opened ~ .header__logo {
    color: #2d383f;
    z-index: 2;
  }
  .header__nav--opened ~ .header__logo:hover {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__logo:focus {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__logo:active {
    color: #0266c1;
  }
  .header__nav--opened ~ .header__contact {
    color: #2d383f;
    z-index: 2;
  }
  .header__nav--opened ~ .header__contact:hover {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__contact:focus {
    color: #1c3374;
  }
  .header__nav--opened ~ .header__contact:active {
    color: #0266c1;
  }
}
@media (max-width: 767px) {
  .header__container {
    display: grid;
    width: 100%;
    padding: 15px 0 50px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 54px 1fr;
    grid-template-areas: "button logo" "button contact" "list list";
  }
  .header__logo {
    color: #ffffff;
    margin: 0;
    margin-left: auto;
    order: 1;
    grid-area: logo;
  }
  .header__logo svg {
    width: 112px;
    height: 34px;
  }
  .header__nav {
    margin-top: 30px;
    grid-area: list;
  }
  .header__nav-list {
    gap: 12px;
  }
  .header__nav-list a {
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.56px;
  }
  .header__contact {
    font-size: 14px;
    line-height: 14px;
    margin-left: auto;
    grid-area: contact;
  }
  .header__nav-button {
    grid-area: button;
    align-self: flex-start;
  }
  .header__nav-button span {
    display: none;
  }
}
.footer {
  width: 100%;
  border-top: 1px solid rgba(44, 56, 63, 0.2);
}

.footer__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 30px 0;
  max-width: 1200px;
  margin: 0 auto;
}

.footer__navigation {
  display: flex;
  align-items: center;
  width: 100%;
  cursor: pointer;
  margin-bottom: 16px;
}

.footer__logo {
  color: #2d383f;
  margin-right: auto;
}
.footer__logo:hover {
  color: #1c3374;
}
.footer__logo:focus {
  color: #1c3374;
}
.footer__logo:active {
  color: #0266c1;
}
.footer__nav {
  display: flex;
  gap: 21px;
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 21px;
  max-width: 600px;
}
.footer__nav-list a {
  padding: 0 5px;
  color: #2d383f;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
}
.footer__nav-list a:hover {
  border-bottom: 1px solid #2d383f;
}
.footer__nav-list a:not(:disabled):focus {
  outline: 1px solid #2d383f;
}
.footer__contact {
  color: #2d383f;
  font-size: 18px;
  line-height: 18px;
  font-weight: 600;
  margin-left: auto;
}
.footer__contact:hover {
  color: #1c3374;
}
.footer__contact:focus {
  color: #1c3374;
}
.footer__contact:active {
  color: #0266c1;
}
.footer__social {
  width: 100%;
  margin-bottom: 20px;
}

.footer__social-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 15px;
  width: 100%;
  justify-content: center;
}
.footer__social-list li {
  display: flex;
  width: 30px;
  height: 30px;
}
.footer__social-list a {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(45, 56, 63, 0.3);
}
.footer__social-list a:hover {
  border-color: #2d383f;
}
.footer__social-list a:focus {
  border-color: #2d383f;
}
.footer__social-list a:active {
  border: none;
  background-color: #0266c1;
}
.footer__social-list a:active svg {
  color: #ffffff;
}

.footer__copyright {
  font-size: 12px;
  line-height: 12px;
  font-weight: 300;
  color: #2d383f;
}

@media (max-width: 1199px) {
  .footer__container {
    padding: 44px 30px 40px;
    max-width: 100%;
  }
  .footer__navigation {
    margin-bottom: 37px;
    justify-content: space-between;
  }
  .footer__logo {
    margin: 0;
  }
  .footer__logo svg {
    width: 118px;
    height: 34px;
  }
  .footer__nav-list {
    gap: 9px;
    max-width: 100%;
  }
  .footer__nav-list a {
    font-size: 14px;
    line-height: 14px;
  }
  .footer__contact {
    display: none;
  }
}
@media (max-width: 767px) {
  .footer__container {
    padding: 30px 0;
    max-width: 100%;
    align-items: flex-start;
    margin: 0 auto;
  }
  .footer__navigation {
    margin-bottom: 30px;
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__logo {
    margin-bottom: 20px;
  }
  .footer__logo svg {
    width: 112px;
  }
  .footer__nav {
    width: 100%;
    display: flex;
    gap: 0;
    justify-content: space-between;
  }
  .footer__nav-list {
    flex-direction: column;
    width: 140px;
  }
  .footer__nav-list a {
    font-size: 14px;
    line-height: 14px;
  }
  .footer__social-list {
    justify-content: flex-start;
  }
  .footer__contact {
    display: none;
  }
